.header{
    height: 60px;
    display: flex;
    align-items: center;
    background-color: lightgoldenrodyellow;
    position: sticky;
    top: 0;
    z-index: 100;
}
.header_logo{
    width: 120px;
    object-fit: contain;
    margin:0px 0px 20px;
    margin-top: 18px;
}
.header_search{
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 25px;
}
.header_searchInput{
    height:12px;
    padding:10px;
    border: none;
    width: 100%;
}
.header_searchInput:focus{
    background-color: whitesmoke;
}
.header_searchIcon {
    color: #fff;
    padding: 5px;
    height: 22px !important;
    background-color: brown;
}
.header_optionLineOne {
    font-size: 14px;

}
.header_optionLineTwo {
    font-size: 15px;
    font-weight: 800;
}
.header_optionBasket {
    display: flex;
    align-items: center;
    color: brown;
}
.header_basketCount {
    margin-left: 10px;
    margin-right: 10px;
}
.header_nav{
    display: flex;
    justify-content: space-evenly;
}
.header_option {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: brown;
}