.login{

    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}



.login_logo{
    margin-bottom: 20px;
    object-fit: contain;
    width: 120px;
    margin-right: auto;
    margin-left: auto;
}

.login_container {
    width: 320px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    border:1px solid lightgrey;
    padding: 20px;

}

.login_contaitner>h1{
    font-weight: 500;
    margin-bottom: 20px;
}

.login_container>form>h5 {
    margin-bottom: 5px;
}

.login_container > form >input {
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
}

.login_container > p {
    margin-top: 15px;
    font-size: 12px;
}

.login_signInButton {
    background: brown;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: wheat;
    color: white;
}