*{
  margin: 0;
}
body {
  background-color: rgb(234, 237, 237);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.header{
    height: 60px;
    display: flex;
    align-items: center;
    background-color: lightgoldenrodyellow;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 100;
}
.header_logo{
    width: 120px;
    object-fit: contain;
    margin:0px 0px 20px;
    margin-top: 18px;
}
.header_search{
    display: flex;
    flex: 1 1;
    align-items: center;
    border-radius: 25px;
}
.header_searchInput{
    height:12px;
    padding:10px;
    border: none;
    width: 100%;
}
.header_searchInput:focus{
    background-color: whitesmoke;
}
.header_searchIcon {
    color: #fff;
    padding: 5px;
    height: 22px !important;
    background-color: brown;
}
.header_optionLineOne {
    font-size: 14px;

}
.header_optionLineTwo {
    font-size: 15px;
    font-weight: 800;
}
.header_optionBasket {
    display: flex;
    align-items: center;
    color: brown;
}
.header_basketCount {
    margin-left: 10px;
    margin-right: 10px;
}
.header_nav{
    display: flex;
    justify-content: space-evenly;
}
.header_option {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: brown;
}
.home{
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}
.home_row {
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}
.home_image{
    width: 100%;
    z-index: -1;
    margin-bottom: -150px;
    -webkit-mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
            mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
}
.sort {
    display: flex;
    flex-direction: column;
    background-color: white;
    z-index: 1;

    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
}
.sort img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}
.btn {
    background: brown;
    border: 1px solid;
    margin-top: 10px;
    border-color: lightgoldenrodyellow;
    color: wheat;
    text-decoration: none;
    padding: 5px;
}
.sort_rating {
    display: flex;
}
.sort_price {
    margin-top: 5px;
}

.sort_info{
    height: 100px;
    margin-bottom: 15px;
}
.checkout {
    display: flex;
    padding: 20px;
    background-color: white;
    height: -webkit-max-content;
    height: max-content;
    position: relative;
}
.checkout_title{
    position: absolute;
    top: 20px;
    left: 5%;
    
}
.checkout_ad {
    width: 100%;
}
.checkoutProduct {
    display: flex;

}
.checkoutProduct_info{
    padding-left: 20px;
}

.checkoutProduct img{
    width: 40%;
    object-fit: contain;
}

.checkoutProduct_info > button {
    background: brown;
    border: 1px solid;
    margin-top: 10px;
    border-color: wheat;
    color: bisque;

}

.checkoutProduct_rating {
    display: flex;
}
.checkoutProduct_tilte {
    font-size: 17px;
    font-weight: 800;
}
.subtotal {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 300px;
    height: 100px;
    padding: 20px;
    background-color: #f3f3f3;
    border: 1px solid #dddddd;
    border-radius: 3px;
}

.subtotal_gift {
    display: flex;
    align-items: center;
}
.subtotal_gift > input{
    margin-right: 5px;
}
.subtotal > button {
    background: brown;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: lightgoldenrodyellow;
    color: wheat;
}
.product {
    display: flex;
    flex-direction: column;
    background-color: white;
    z-index: 1;

    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
}
.product>img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}
.product > button {
    background: brown;
    border: 1px solid;
    margin-top: 10px;
    border-color: lightgoldenrodyellow;
    color: wheat;
}
.product_rating {
    display: flex;
}
.product_price {
    margin-top: 5px;
}

.product_info{
    height: 100px;
    margin-bottom: 15px;
}
.allDataLayout {
    display: flex;
    flex-direction: column;
}

.back-to-school, .holloween, .thanksgiving{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
    grid-gap: 10px;
}
.login{

    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}



.login_logo{
    margin-bottom: 20px;
    object-fit: contain;
    width: 120px;
    margin-right: auto;
    margin-left: auto;
}

.login_container {
    width: 320px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    border:1px solid lightgrey;
    padding: 20px;

}

.login_contaitner>h1{
    font-weight: 500;
    margin-bottom: 20px;
}

.login_container>form>h5 {
    margin-bottom: 5px;
}

.login_container > form >input {
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
}

.login_container > p {
    margin-top: 15px;
    font-size: 12px;
}

.login_signInButton {
    background: brown;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: wheat;
    color: white;
}
