.sort {
    display: flex;
    flex-direction: column;
    background-color: white;
    z-index: 1;

    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
}
.sort img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}
.btn {
    background: brown;
    border: 1px solid;
    margin-top: 10px;
    border-color: lightgoldenrodyellow;
    color: wheat;
    text-decoration: none;
    padding: 5px;
}
.sort_rating {
    display: flex;
}
.sort_price {
    margin-top: 5px;
}

.sort_info{
    height: 100px;
    margin-bottom: 15px;
}